import { useEffect } from 'react'

export function useOutsideListener(ref: any, onClick?: () => void) {
    useEffect(() => {
        function handleClickOutside(event: Event) {
            if (ref.current && !ref.current.contains(event.target) && onClick) {
                onClick()
            }
        }

        // Bind the event listener
        document.addEventListener('mousedown', handleClickOutside)
        return () => {
            // Unbind the event listener on clean up
            document.removeEventListener('mousedown', handleClickOutside)
        }
    }, [onClick, ref])
}

export function useEscapeListener(onEscape?: () => void) {
    useKeyDownListener(onEscape, 27)
}

export function useKeyDownListener(onKeyDown?: () => void, keyCode?: number) {
    useEffect(() => {
        const close = (event: KeyboardEvent) => {
            if (!keyCode || event.keyCode === keyCode) {
                onKeyDown && onKeyDown()
            }
        }
        window.addEventListener('keydown', close)
        return () => window.removeEventListener('keydown', close)
    }, [keyCode, onKeyDown])
}
