import styles from './navigation.module.scss'
import './navigation.scss'
import Menu from '../Menu'
import Button from '../Button'
import { Icon } from '@iconify/react'
import { useState } from 'react'
import classNames from 'classnames'
import ClickAwayListener from '../ClickAwayListener'

function Navigation() {
    const [menuVisible, setMenuVisible] = useState(false)

    return (
        <>
            <Button className={styles.NavigationButton} onClick={() => setMenuVisible(mv => !mv)}>
                <>
                    {!menuVisible && <Icon icon="eva:menu-outline" color="#FFFFFF" fontSize={30} />}
                    {menuVisible && (
                        <Icon icon="clarity:close-line" color="#02C4EC" fontSize={30} />
                    )}
                </>
            </Button>
            <ClickAwayListener
                onClick={() => setMenuVisible(false)}
                className={classNames(styles.Navigation, {
                    [styles.NavigationVisible]: menuVisible,
                })}
            >
                <Menu
                    onItemClick={() => setMenuVisible(false)}
                    className={classNames(styles.Navigation, {
                        [styles.NavigationVisible]: menuVisible,
                    })}
                    items={[
                        {
                            title: 'About',
                            to: '/about',
                            icon: 'carbon:data-2',
                        },
                        {
                            title: 'Skills',
                            to: '/skills',
                            icon: 'carbon:skill-level-intermediate',
                            children: [
                                {
                                    title: 'Development',
                                    to: '/development',
                                    icon: 'carbon:code',
                                },
                                {
                                    title: 'Infrastructure',
                                    to: '/infrastructure',
                                    icon: 'carbon:infrastructure-classic',
                                },
                                {
                                    title: 'Analysis',
                                    to: '/analysis',
                                    icon: 'carbon:text-link-analysis',
                                },
                            ],
                        },
                        {
                            title: 'Experience',
                            to: '/experience',
                            icon: 'carbon:certificate',
                        },
                        {
                            title: 'Team',
                            to: '/team',
                            icon: 'carbon:user',
                        },
                        {
                            title: 'Contacts',
                            to: '/contact',
                            icon: 'carbon:email',
                        },
                    ]}
                />
            </ClickAwayListener>
        </>
    )
}

export default Navigation
