import styles from './header.module.scss'

// import { Icon } from '@iconify/react'

interface HeaderProps {
    readonly level?: 1 | 2 | 3
    readonly icon?: string
    readonly children?: JSX.Element | string
}

function Header({ level = 1, icon: _, children }: HeaderProps) {
    const content = (
        <>
            {/*{icon && <Icon icon={icon} color={'#575965'} />}*/}
            {children}
        </>
    )
    switch (level) {
        case 1:
            return <h1 className={styles.Header}>{content}</h1>
        case 2:
            return <h2 className={styles.Header}>{content}</h2>
        case 3:
            return <h3 className={styles.Header}>{content}</h3>
    }
}

export default Header
