import MenuItem from './menu-item'
import styles from './menu.module.scss'
import './menu.scss'
import classNames from 'classnames'

export interface MenuItemModel {
    readonly title: string
    readonly to?: string
    readonly children?: MenuItemModel[]
    readonly icon?: string
}

interface MenuProps {
    readonly items: MenuItemModel[]
    readonly className?: string
    readonly onItemClick?: () => void
}

function Menu({ items, className, onItemClick }: MenuProps) {
    return (
        <ul className={classNames(styles.Menu, className)}>
            {items.map(item => (
                <MenuItem item={item} key={item.to + item.title} onItemClick={onItemClick} />
            ))}
        </ul>
    )
}

export default Menu
